
.com-contain {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-flow: row;
          flex-flow: row;
  margin: 5px 5px;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  font-size: 16px;
  overflow: hidden;
}
.com-contain .com-menu {
    /*float: left;*/
    font-family: "Microsoft YaHei";
    margin-right: 5px;
    border: 1px solid #cdcdcd;
    padding: 10px 0;
    border-radius: 10px;
    width: 160px;
}
.com-contain .com-menu li {
      list-style: none;
      line-height: 40px;
}
.com-contain .com-menu a {
      text-decoration: none;
      display: block;
      padding: 0 10px;
      color: #5e5e5e;
}
.com-contain .com-menu a.router-link-active {
        background: #d5d5d5;
        color: #5e5e5e !important;
}
.com-contain .com-view {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    overflow: auto;
}



