
.svg-pos {
  position: relative;
}
circle {
  transition: stroke-dasharray ease-in-out 1s;
}
.percentColor {
  position: absolute;
  width: 100%;
  height: 30px;
  top: 50%;
  text-align: center;
  margin-top: -15px;
  line-height: 30px;
  font-weight: bold;
  color: #515151;
}



