
.v-enter {
  opacity: 0;
  -webkit-transform: translateY(-0.5rem);
          transform: translateY(-0.5rem);
}
.v-enter-active {
  transition: all .5s;
}
.v-leave-active {
  opacity: 0;
  transition: all .5s;
  -webkit-transform: translateY(-0.5rem);
          transform: translateY(-0.5rem);
}
.wrap-border {
  overflow: hidden;
  border: 1px solid #cdcdcd;
  padding: 0 20px 20px 20px;
  border-radius: 10px;
  margin-bottom: 10px;
}
.wrap-border h4 {
    text-align: center;
    font-size: 16px;
    color: #aaa;
    line-height: 40px;
}



